import React, { Component } from "react";
import "../App.css";
import axios from "axios";
import { Button, Header, Form } from "semantic-ui-react";

const liff = window.liff;
const API = "https://line-smartfarm-api.herokuapp.com";

export default class register extends Component {
  // state
  constructor(props) {
    super(props);
    this.state = {
      line_id: "",
      line_pic: "",
      name: "",
      tel: "",
      loading: false
    };
    this.initialize = this.initialize.bind(this);
  }

  // init state (use to get liff data)
  initialize() {
    console.log("Entering initialize state...");
    liff.init({ liffId: "1610155283-WqRpOKwB" }, async () => {
      let profile = await liff.getProfile();
      this.setState({
        line_id: profile.userId,
        line_pic: profile.pictureUrl
      });
      console.log("Get UID completed going to register page...");
      this.verifyUID();
    });
  }

  componentDidMount() {
    window.addEventListener("load", this.initialize);
    document.title = "Register";
  }

  // use to check form change
  changeHandler = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  // post a form data to DB
  userPost = e => {
    console.log("Connecting to an API...");
    e.preventDefault();
    this.setState({ loading: true });
    axios
      .post(API + "/users", this.state.line_id, this.state.name, this.state.tel)
      .then(res => {
        console.log(res);
        console.log("Register Success!");

        // delay before close
        setTimeout(() => {
          this.setState({ loading: false });
          liff.closeWindow();
        }, 2000);
      })
      .catch(err => {
        console.log(err);
        console.log("Register Failed!");
        this.setState({ loading: false });
      });
  };

  render() {
    return (
      <div className="main">
        <Header as='h1' className="prompt">ลงทะเบียนผู้ใช้ใหม่</Header>
        <Form onSubmit={this.userPost}>
          <Form.Field>
            <input className="form-input" value={this.state.line_id} disabled/>
          </Form.Field>
          <Form.Field required>
            <label className="form-label">ชื่อเกษตรกร</label>
            <input
              required
              className="form-input"
              placeholder="กรอกชื่อที่นี่"
              type="text"
              name="name"
              value={this.state.name}
              disabled={this.state.loading}
              onChange={this.changeHandler}
            />
          </Form.Field>
          <Form.Field required>
            <label className="form-label">เบอร์โทรศัพท์</label>
            <input
              required
              className="form-input"
              placeholder="กรอกเบอร์โทรศัพท์ที่นี่"
              maxLength="10"
              type="tel"
              name="tel"
              value={this.state.tel}
              disabled={this.state.loading}
              onChange={this.changeHandler}
            />
          </Form.Field>
          <Button
            color="orange"
            content="ลงทะเบียน"
            className="form-btn"
            type="submit"
            disabled={this.state.loading}
            loading={this.state.loading}
          />
        </Form>
      </div>
    );
  }
}
